import { Provider } from '@ethersproject/abstract-provider';
import { BigNumberish } from '@ethersproject/bignumber';
import { Contract } from '@ethersproject/contracts';
import { Transaction } from '@ethersproject/transactions';
import { Wallet } from '@ethersproject/wallet';
import RainbowRouterABI from './abi/RainbowRouter.json';
import {
  API_BASE_URL,
  ETH_ADDRESS,
  RAINBOW_ROUTER_CONTRACT_ADDRESS,
  WETH,
} from './utils/constants';
import { signPermit } from '.';

export enum Sources {
  Aggregator0x = '0x',
  Aggregotor1inch = '1inch',
}

export type EthereumAddress = string;

export interface QuoteParams {
  source?: Sources;
  chainId: number;
  fromAddress: EthereumAddress;
  sellTokenAddress: EthereumAddress;
  buyTokenAddress: EthereumAddress;
  sellAmount?: BigNumberish;
  buyAmount?: BigNumberish;
  slippage: number;
}

export interface ProtocolShare {
  name: string;
  part: number;
}

export interface Quote {
  source?: Sources;
  from: EthereumAddress;
  to?: EthereumAddress;
  data?: string;
  value?: BigNumberish;
  allowanceTarget?: EthereumAddress;
  sellAmount: BigNumberish;
  sellAmountMinusFees: BigNumberish;
  sellTokenAddress: EthereumAddress;
  buyTokenAddress: EthereumAddress;
  buyAmount: BigNumberish;
  fee: BigNumberish;
  feePercentageBasisPoints: number;
  protocols?: ProtocolShare[];
  inputTokenDecimals?: number;
  outputTokenDecimals?: number;
}

export interface TransactionOptions {
  gasLimit?: string | number;
  gasPrice?: string;
  nonce?: number;
  value?: number | BigNumberish;
  from?: EthereumAddress;
}

export interface QuoteExecutionDetails {
  method: any;
  methodArgs: (string | number | BigNumberish)[];
  params: TransactionOptions;
}

export const getQuote = async ({
  source,
  chainId = 1,
  fromAddress,
  sellTokenAddress,
  buyTokenAddress,
  sellAmount,
  buyAmount,
  slippage,
}: QuoteParams): Promise<Quote | null> => {
  // Wrapping or Unwrapping ETH
  if (
    (sellTokenAddress === ETH_ADDRESS && buyTokenAddress === WETH['1']) ||
    (sellTokenAddress === WETH['1'] && buyTokenAddress === ETH_ADDRESS)
  ) {
    return {
      buyAmount: sellAmount || buyAmount,
      buyTokenAddress,
      fee: 0,
      feePercentageBasisPoints: 0,
      from: fromAddress,
      inputTokenDecimals: 18,
      outputTokenDecimals: 18,
      sellAmount: sellAmount || buyAmount,
      sellAmountMinusFees: sellAmount || buyAmount,
      sellTokenAddress,
    } as Quote;
  }

  let url = `${API_BASE_URL}/quote?chainId=${chainId}&fromAddress=${fromAddress}&buyToken=${buyTokenAddress}&sellToken=${sellTokenAddress}&slippage=${slippage}`;
  if (source) {
    url += `&source=${source}`;
  }
  if (sellAmount) {
    url += `&sellAmount=${sellAmount}`;
  } else if (buyAmount) {
    url += `&buyAmount=${buyAmount}`;
  }
  if (isNaN(Number(sellAmount)) && isNaN(Number(buyAmount))) {
    return null;
  }
  const response = await fetch(url);
  const quote = await response.json();
  return quote as Quote;
};

export const fillQuote = async (
  quote: Quote,
  transactionOptions: TransactionOptions,
  wallet: Wallet,
  permit: boolean,
  chainId: number
): Promise<Transaction> => {
  const instance = new Contract(
    RAINBOW_ROUTER_CONTRACT_ADDRESS,
    RainbowRouterABI,
    wallet
  );
  let swapTx;

  const {
    sellTokenAddress,
    buyTokenAddress,
    to,
    data,
    fee,
    value,
    sellAmount,
    feePercentageBasisPoints,
    allowanceTarget,
  } = quote;

  if (sellTokenAddress?.toLowerCase() === ETH_ADDRESS.toLowerCase()) {
    swapTx = await instance.fillQuoteEthToToken(
      buyTokenAddress,
      to,
      data,
      fee,
      {
        ...transactionOptions,
        value,
      }
    );
  } else if (buyTokenAddress?.toLowerCase() === ETH_ADDRESS.toLowerCase()) {
    if (permit) {
      const { timestamp } = await wallet.provider.getBlock('latest');
      const deadline = timestamp + 3600;
      const permitSignature = await signPermit(
        wallet,
        sellTokenAddress,
        quote.from,
        instance.address,
        quote.sellAmount.toString(),
        deadline,
        chainId
      );
      swapTx = await instance.fillQuoteTokenToEthWithPermit(
        sellTokenAddress,
        allowanceTarget,
        to,
        data,
        sellAmount,
        feePercentageBasisPoints,
        [permitSignature.nonce, deadline],
        permitSignature.v,
        [permitSignature.r, permitSignature.s],
        {
          ...transactionOptions,
          value,
        }
      );
    } else {
      swapTx = await instance.fillQuoteTokenToEth(
        sellTokenAddress,
        allowanceTarget,
        to,
        data,
        sellAmount,
        feePercentageBasisPoints,
        {
          ...transactionOptions,
          value,
        }
      );
    }
  } else {
    if (permit) {
      const { timestamp } = await wallet.provider.getBlock('latest');
      const deadline = timestamp + 3600;
      const permitSignature = await signPermit(
        wallet,
        sellTokenAddress,
        quote.from,
        instance.address,
        quote.sellAmount.toString(),
        deadline,
        chainId
      );

      swapTx = await instance.fillQuoteTokenToToken(
        sellTokenAddress,
        buyTokenAddress,
        allowanceTarget,
        to,
        data,
        sellAmount,
        fee,
        [permitSignature.nonce, deadline],
        permitSignature.v,
        [permitSignature.r, permitSignature.s],
        {
          ...transactionOptions,
          value,
        }
      );
    } else {
      swapTx = await instance.fillQuoteTokenToToken(
        sellTokenAddress,
        buyTokenAddress,
        allowanceTarget,
        to,
        data,
        sellAmount,
        fee,
        {
          ...transactionOptions,
          value,
        }
      );
    }
  }
  return swapTx;
};

export const getQuoteExecutionDetails = (
  quote: Quote,
  transactionOptions: TransactionOptions,
  provider: Provider
): QuoteExecutionDetails => {
  const instance = new Contract(
    RAINBOW_ROUTER_CONTRACT_ADDRESS,
    RainbowRouterABI,
    provider
  );

  const {
    sellTokenAddress,
    buyTokenAddress,
    to,
    data,
    fee,
    value,
    sellAmount,
    feePercentageBasisPoints,
    allowanceTarget,
  } = quote;

  if (sellTokenAddress?.toLowerCase() === ETH_ADDRESS.toLowerCase()) {
    return {
      method: instance.estimateGas['fillQuoteEthToToken'],
      // @ts-ignore
      methodArgs: [buyTokenAddress, to, data, fee],
      params: {
        ...transactionOptions,
        value,
      },
    };
  } else if (buyTokenAddress?.toLowerCase() === ETH_ADDRESS.toLowerCase()) {
    return {
      method: instance.estimateGas['fillQuoteTokenToEth'],
      methodArgs: [
        sellTokenAddress, // @ts-ignore next-line
        allowanceTarget, // @ts-ignore next-line
        to, // @ts-ignore next-line
        data,
        sellAmount,
        feePercentageBasisPoints,
      ],
      params: {
        ...transactionOptions,
        value,
      },
    };
  } else {
    return {
      method: instance.estimateGas['fillQuoteTokenToToken'],
      methodArgs: [
        sellTokenAddress,
        buyTokenAddress, // @ts-ignore next-line
        allowanceTarget, // @ts-ignore next-line
        to, // @ts-ignore next-line
        data, // @ts-ignore next-line
        sellAmount,
        fee,
      ],
      params: {
        ...transactionOptions,
        value,
      },
    };
  }
};
